@Injectable({
  providedIn: 'root'
})

export class CollegeService {
  private url = '/room/search?'

  getRooms(terms){    
    let query='college=' + terms.college + '&'
    if (terms.room != '')
      query +='building=' + terms.room.toUpperCase() + '&'
    if (terms.duration != '')
      query +='duration=' + terms.duration
    console.log(this.url + query)
    return this.http.get<Classroom[]>(this.url + query)
    // return of(rooms)
  }

  getColleges(){
    let college_url = '/college'
    return this.http.get<College[]>(college_url)
    // return of(colleges)
  }

  constructor(private http:HttpClient) { }
}
