import { Component, OnInit } from '@angular/core';
import { CollegeService } from '../college.service';
import { Observable, Subject} from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';

class Classroom {
  room: string
  duration: number
  status: string
}

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})

export class MainComponent implements OnInit {
  private colleges = []
  private classrooms: Classroom[] = []
  private searchTerms = new Subject<string>();
  private TextBoxDisabled = true
  private msg = 'No search criteria';
  private msgDur = ''
  private roomSortDir = 1
  private durSortDir = -1
  private sortedMsg = ''
  math = Math
  private Searching = false;

  constructor(private collegeService:CollegeService) { }

  search(terms){        
    const json = JSON.stringify(terms)
    this.searchTerms.next(json)    
    this.msg = 'Looking up course catalog..'  
    this.Searching = true
  }

  sort(property, dir){
    // console.log(property, this.classrooms[0][property])
    this.classrooms.sort((a, b) => (a[property] > b[property]) ? dir * 1 : dir * -1)

    this.sortedMsg = '';

    if(property == 'room'){
      this.sortedMsg += ' Building/Floor: '
      if(dir == -1)
        this.sortedMsg += 'Ascending.'
      else
        this.sortedMsg += 'Decscending.'
    }
    else {
      this.sortedMsg += ' Duration: '
      if(dir == -1)
        this.sortedMsg += 'Ascending.'
      else
        this.sortedMsg += 'Decscending.'
    }
  }

  // getRooms(){
  //   return this.collegeService.getRooms();
  // }

  ngOnInit() {
    //set up subject
    this.searchTerms.pipe(
      debounceTime(300  ),
      distinctUntilChanged(),
      switchMap(json => {
        let terms = JSON.parse(json)
        this.msg = terms.room.toUpperCase() + ' ' + this.colleges[terms.college - 1].name
        this.msgDur = terms.duration + ' minutes '
        terms.college = this.colleges[terms.college - 1]._id
        this.Searching = false
        return this.collegeService.getRooms(terms)
      })
    ).subscribe(rooms => {     
      this.classrooms = rooms       
      this.sort('duration', -1)
      // this.msg = this.classrooms.length + ' empty classrooms found for "' + this.msg
      
      console.log(this.classrooms)
    })

    //populate select w/ colleges
    this.collegeService.getColleges().subscribe(colleges => this.colleges = colleges)
  }

}


